import { GET_DOGS, GET_TEMPS } from "../actions/actionsTypes.js";

const initialState = {
  allDogs: [],
  dogsToFilter: [],
  temperaments: [],
  detail: {},
};

function rootReducer(state = initialState, action) {
  switch (action.type) {
    case GET_DOGS:
      return {
        ...state,
        allDogs: action.payload,
        dogsToFilter: action.payload,
      };
    case GET_TEMPS:
      return {
        ...state,
        temperaments: action.payload,
      };
    default:
      return state;
  }
}

export default rootReducer;
